
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.body{
  width: 100vw;
  height: 100vh;
  background-color: rgb(10, 7, 7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.card{
  width: 56vw;
  height: 41vh;
  background-color:   rgb(9, 14, 14);
  display: flex;
  border-radius: 8px;
  margin-left: 15px;
  margin-top: 10px;

  
}

.left{
  width: 55%;
  height: 100%;
  background-color:   rgb(9, 14, 14);
  border-radius: 8px;
}

.upper{
  width: 100%;
  height: 40%;
  background-color:    rgb(9, 14, 14);
  display: flex;
  border-radius: 8px;
}


.S-img{
  width:20%;
  height: 80%;
  background-color:  rgb(9, 14, 14) ;
  display: flex;
  align-items: center;
  padding-left: 10px;
  padding-top: 25px;
 
}
.imges{
  width: 72px;
  height: 100px;
}

.M-name{
  padding-bottom: 50px;
  padding-left: 5px;
  width:80%;
  height:100% ;
  background-color:  rgb(9, 14, 14);
  padding-bottom: 75px;
  color: rgb(236, 235, 235);
  font-family: 'Montserrat', sans-serif;
}

.time{
  padding-top: 15px;
  font-size: 13px;
}

.Bright{
 
}

.year{
  color: rgb(49, 49, 255);
}

.btn{
  background-color: black;
  color: rgb(189, 187, 187);
  border: solid 2px rgb(54, 41, 41);
  width: 50px;
  height: 20px;
  border-radius: 5px;
}

.btn-content{
  font-size: 0.75rem;
}
.middle{
  width: 100%;
  height: 50%;
  background-color:   rgb(9, 14, 14);
  display: flex;
 
}

.about-bright{
  width: 85%;
  margin-left: 14px;
  margin-top: 15px;
  color: rgb(247, 248, 248);
  font-family: 'Montserrat', sans-serif;
  font-size: 12px;
}
.lower{
  width: 100%;
  height: 10%;
  background-color:   rgb(9, 14, 14);
  display: flex;
  flex-direction: row;
  padding-left: 18px;
  align-items: center;
  gap: 25px;
  border-radius: 7px;
}

.right{
  width: 45%;
  height: 100%;
  background-color:  rgb(9, 14, 14);
  border-radius: 7px;
}

.rightimg{
  width: 100%;
  height: 100%;
  border-radius: 7px;
}

